import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormArray } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';

import { EventsService } from '../events.service';

@Component({
  selector: 'app-add-event',
  templateUrl: './add-event.component.html',
  styleUrls: ['./add-event.component.scss'],
})
export class AddEventComponent implements OnInit {
  image;
  constructor(
    private fb: FormBuilder,
    private eventsService: EventsService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {}

  form = this.fb.group({
    image: [],
    name: ['', Validators.required],
    description: ['', Validators.required],
    voting: [false, Validators.required],
  });

  ngOnInit(): void {}

  onSubmit() {
    this.spinner.show();
    console.log(this.form.value);
    const formData = new FormData();

    for (const key in this.form.value) {
      if (this.form.value[key] instanceof File) {
        formData.append(key, this.form.value[key]);
      } else {
        // contains object
        formData.append(key, JSON.stringify(this.form.value[key]));
      }
    }

    this.eventsService.addOne(formData).subscribe(
      (res) => {
        console.log(res);
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['/events']);
        this.spinner.hide();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
        this.spinner.hide();
      }
    );
    this.spinner.hide();
  }
}
